import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.scss']
})
export class TestComponent implements OnInit {
  withBorder = true;
  textColor = 'red';
  onMouseClick() {
    this.withBorder = !this.withBorder;
  }
  onMouseOver(){
    this.textColor = 'blue';
  }
  onMouseOut(){
    this.textColor = 'red';
  }
  constructor() { }

  ngOnInit() {
  }

}
