import { Component, OnInit, Input, OnChanges, SimpleChange, SimpleChanges, Output, EventEmitter, OnDestroy, AfterViewInit, AfterContentInit, AfterViewChecked, AfterContentChecked } from '@angular/core';
import { DataService } from '../services/data.service';


@Component({
  selector: 'app-hello',
  template: `
  <h2>Hello from Hello Component</h2>
  <p>{{text}} from App Component</p>
  <button (click)="onButtonHelloClicked()">Button in Hello</button>
  `,
  styleUrls: ['./hello.component.scss']
})
export class HelloComponent
  implements OnInit, OnChanges, OnDestroy, AfterViewInit, AfterContentInit, AfterViewChecked, AfterContentChecked {
  @Input()text: string;
  @Output() buttonHelloClicked: EventEmitter<any> = new EventEmitter<any>();
  onButtonHelloClicked() {
    this.text = 'Changed from Hello Component';
    this.buttonHelloClicked.emit(this.text);
    this._dataService.setTextFromHello(this.text);
  }
  constructor(private _dataService: DataService) {
  }
  ngOnInit(): void {
    console.log('HELLO OnInit ran');
    this._dataService.setTextFromHello(this.text);
  }
  ngOnChanges(changes: SimpleChanges): void {
    // console.log('HELLO OnChange ran',{changes});
  }
  ngOnDestroy(): void {
    // console.log('HELLO OnDestroy ran');
  }
  ngAfterViewInit(): void {
    // console.log('HELLO AfterViewInit ran');
  }
  ngAfterViewChecked(): void {
    // console.log('HELLO AfterViewChecked ran');
  }
  ngAfterContentInit(): void {
    // console.log('HELLO AfterContentInit ran');
  }
  ngAfterContentChecked(): void {
    // console.log('HELLO AfterContentChecked ran');
  }
}
