import { Component, OnInit} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-sign-in',
  template: `
  <form (ngSubmit)="onSubmit(SignInForm)" #SignInForm="ngForm">
  <input placeholder="Email" #txtEmail="ngModel" ngModel name="email" required email>
  <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
    Email is required
  </p>
  <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
  Email is wrong
  </p>
  <br><br>
  <input type="password"
    placeholder="Password"
    #txtPassword="ngModel"
    ngModel
    name="password"
    minlength="6"
    pattern="[a-z]*">
  <br><br>
  <div ngModelGroup="Subject">
    <label><input type="checkbox" [ngModel]="false" name="NodeJS ">NodeJS</label>
    <label><input type="checkbox" [ngModel]="false" name="Angular">Angular</label>
    <label><input type="checkbox" [ngModel]="false" name="React">React</label>
  </div>
  <br>
  <button [disabled]="SignInForm.invalid">Submit</button>
  </form>
  <p>{{ txtEmail.errors | json}}</p>
  <p>{{ txtPassword.errors | json}}</p>
  <p>{{ SignInForm.value | json}}</p>
  `,

})
export class SignInComponent implements OnInit {
  constructor(private http: HttpClient) {}
  onSubmit(SignInForm) {
    console.log(SignInForm);
  }
  ngOnInit() {
  }

}
