import { Component, OnInit, OnChanges, OnDestroy, AfterContentChecked, AfterContentInit, AfterViewInit, AfterViewChecked, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent
  implements OnInit, OnChanges, OnDestroy, AfterViewInit, AfterContentInit, AfterViewChecked, AfterContentChecked {
  title = 'Title cua App Component';
  withBorder = true;
  textColor = 'red';
  onButtonClickedFromHello(event: string) {
    console.log({event}, 'click from hello');
    this.title = event
  }
  onMouseClick() {
    this.withBorder = !this.withBorder;
    this.title = 'Changed'
  }
  onMouseOver(){
    this.textColor = 'blue';
  }
  onMouseOut(){
    this.textColor = 'red';
  }
  ngOnInit(): void{
    // console.log('APP OnInit ran');
  }
  ngOnChanges(changes: SimpleChanges): void{
    // console.log('APP OnChange ran',{changes});
  }
  ngOnDestroy(): void{
    // console.log('APP OnDestroy ran');
  }
  ngAfterViewInit(): void{
    // console.log('APP AfterViewInit ran');
  }
  ngAfterViewChecked(): void{
    // console.log('APP AfterViewChecked ran');
  }
  ngAfterContentInit(): void{
    // console.log('APP AfterContentInit ran');
  }
  ngAfterContentChecked(): void{
    // console.log('APP AfterContentChecked ran');
  }
}
